import http from "../http-common";
import authHeader from './auth-header';

class ResidentDataService {
  getAll() {
    return http.get("/residents", { headers: authHeader() });
  }

  get(id) {
    return http.get(`/residents/${id}`, { headers: authHeader() });
  }

  create(data) {
    return http.post("/residents", data, { headers: authHeader() });
  }

  update(id, data) {
    return http.put(`/residents/${id}`, data, { headers: authHeader() });
  }

  delete(id) {
    return http.delete(`/residents/${id}`, { headers: authHeader() });
  }

  findByLocation(id) {
    return http.get(`/residents/location/${id}`, { headers: authHeader() });
  }
}

export default new ResidentDataService();