import http from "../http-common";
import authHeader from './auth-header';

class OptionsDataService {
  getAll() {
    return http.get("/options", { headers: authHeader() });
  }

  get(id) {
    return http.get(`/options/${id}`, { headers: authHeader() });
  }

  create(data) {
    return http.post("/options", data, { headers: authHeader() });
  }

  update(id, data) {
    return http.put(`/options/${id}`, data, { headers: authHeader() });
  }

  delete(id) {
    return http.delete(`/options/${id}`, { headers: authHeader() });
  }


}

export default new OptionsDataService();